.formWrapper {
  width: 100%;
  height: 100vh;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #4ca1af;
  background: -webkit-linear-gradient(to right, #c4e0e5, #4ca1af);
  background: linear-gradient(to right, #c4e0e5, #4ca1af);
}
.formInnerWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 50px 50px 10px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.5);
}
.userAvt {
  border-radius: 50px;
}
.formInnerWrapper input {
  width: 100%;
  margin-bottom: 18px;
  height: 40px;
  padding: 4px;
  font-size: 18px;
  background-color: white;
  border-radius: 6px;
}
#submitButton {
  width: 150px;
  font-size: 24px;
  cursor: pointer;
  height: 50px;
  background-color: rgba(31, 43, 209, 0.5);
  margin-top: 25px;
}
#submitButton:hover {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}
